.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #f5f7fb;
}

.start-page {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.start-page h3 {
  font-family: "Karla", sans-serif;
  font-size: 31.25px;
  line-height: 37px;
  color: #293264;
  margin: 0;
}
.start-page p {
  font-family: "Karla", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #293264;
}
.start-page button {
  font-family: "Karla", sans-serif;
  background: #4d5b9e;
  border-radius: 15px;
  border: none;
  padding: 1rem 3rem;
  color: white;
  cursor:pointer;
}
.quiz-box {
  width: 70%;
  margin: auto;
  padding: 20px;
  border-top: 1.5px solid #dbdef0;
  border-bottom: 1.5px solid #dbdef0;
}
.quiz-box > p {
  font-family: "Karla";
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #293264;
}
.quiz-type {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #e2e8f0;
  border-radius: 10px;
}
.quiz-options {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
}
.quiz-type span {
  border-radius: 10px;
  border: 1.5px solid #1e293b;
  padding: 5px;
  margin: 10px;
  font-family: "karla", sans-serif;
  font-size: 10.24px;
  line-height: 12px;
  text-align: center;
  color: black;
}
.quiz-options li {
  list-style: none;
  border-radius: 10px;
  border: 1.5px solid #4d5b9e;
  cursor: pointer;
  padding: 5px;
  margin: 10px;
  font-family: "Inter";
  font-weight: bold;
  font-size: 10.24px;
  line-height: 12px;
  text-align: center;
  color: #293264;
}
.selected {
  background: #d6dbf5;
}
button{
  background: #4D5B9E;
  border-radius: 10px;
  border:none;
  padding:1rem 2rem;
  color:white;
}
.button{
margin:20px 0;
}
.correct-answer{
background-color: #94D7A2;
}
.wrong-answer{
  background-color: #F8BCBC;
}
.loader-image{
  position:absolute;
  top:50%;
}
.quiz-ouput{
  display: flex;
  justify-content: center;
  align-items:center;
  font-family: "karla";
  font-size:16px;
  flex-direction: column;
  font-weight: bold;
}
.quiz-ouput span{
  margin:5px;
  display: inline-block;
}